import Page from 'components/Page';
import React from 'react';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
} from 'reactstrap';
import states from "../api/states";
import { updateUser, updatePwd, } from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";

class ProfilePage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          fname : '',
          lname : '',
          address : '',
          city:'',
          state:'nn',
          zip:'',
          email:'',
          phone:'',
          password:'',
          c_password:'',
          xtoken:'',
          msg: '',
          error: '',
          msg2: '',
          error2: '',
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
                ...this.state,
                fname: obje_results.user.fname,
                lname: obje_results.user.lname,
                address: obje_results.user.address,
                city: obje_results.user.city,
                state: obje_results.user.state,
                zip: obje_results.user.zip,
                email: obje_results.user.email,
                phone: obje_results.user.phone,
                xtoken : obje_results.user.token,
            });
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    isValidEmailAddress = (email) => {
        if (/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
            return true;
        }
        return false;
    }

    handlePasswordInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, password: v,});
    }
    handleCpasswordInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, c_password: v,});
    }
    handleFnameInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, fname: v,});
        // console.log(v);
    }
    handleLnameInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, lname: v,});
        // console.log(v);
    }
    handleAddressInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, address: v,});
    }
    handleCityInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, city: v,});
    }
    handleStateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, state: v,});
    }
    handleZipInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, zip: v,});
    }
    handleEmailInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, email: v,});
    }
    handlePhoneInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, phone: v,});
    }
    handleFaxInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, fax: v,});
    }
    handleChangePwd = () => {
        updatePwd(this.state.xtoken, this.state)
        .then((response) => {
            if( response.status === 200)
            {
                this.setState({
                    ...this.state,
                    msg2: response.message,
                    error2: '',
                });

                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error2: response.message,
                    msg2: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
            });
            console.log('error', error);
        });
    }
    handleSaveChanges = () => {
        updateUser(this.state.xtoken, this.state)
        .then((response) => {
            if( response.status === 200)
            {
                let newstate = {
                    user : response.data,
                    is_logged_in : true,
                };
                AsyncStorage.setItem(conf.secret, JSON.stringify(newstate)).then((re) => {})
                .catch((errrr) => {});
                this.setState({
                    ...this.state,
                    msg: response.message,
                    error: '',
                });

                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    msg: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
            });
            console.log('error', error);
        });
    }
    render() {
      return (
        <Page title="User Profile" breadcrumbs={[{ name: 'Profile', active: true }]}>
          <Row>
            <Col xl={4} lg={12} md={12}>
              <Card>
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
                {this.state.msg.length > 0 && (
                    <Alert color="success">{this.state.msg}</Alert>
                )}
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleEmail">First name*</Label>
                      <Input onChange={this.handleFnameInput}
                        type="text" 
                        value={this.state.fname}
                        name="fname"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Last name*</Label>
                      <Input onChange={this.handleLnameInput}
                        type="text" 
                        value={this.state.lname}
                        name="lname"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Address*</Label>
                      <Input onChange={this.handleAddressInput}
                        type="text"
                        value={this.state.address}
                        name="address"
                        placeholder="e.g. 3015 Lanely park"
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>

            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleUrl">City*</Label>
                      <Input onChange={this.handleCityInput}
                        type="text" 
                        value={this.state.city}
                        name="city"
                        id="city"
                        placeholder="e.g. Plano"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleSelect">State*</Label>
                      <Input onChange={this.handleStateInput} value={this.state.state} defaultValue={this.state.state} type="select" name="state">
                        <option value="nn" disabled={true}>Select state</option>
                        {
                            states.data.map((stt, idx) => (
                                <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleNumber">Zip Code*</Label>
                      <Input onChange={this.handleZipInput}
                        type="number"
                        value={this.state.zip}
                        name="zip"
                        id="zip"
                        placeholder="zip code"
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
    
            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="examplePassword">Email address*</Label>
                      <Input onChange={this.handleEmailInput}
                        type="email"
                        value={this.state.email}
                        name="email"
                        placeholder="a.valid@email.com"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Phone number*</Label>
                      <Input onChange={this.handlePhoneInput}
                        type="text"
                        value={this.state.phone}
                        name="phone"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleSaveChanges}>Save changes</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>


          <Row>
            <Col xl={6} lg={12} md={12}>
              <Card>
                <CardHeader>Change password</CardHeader>
                {this.state.error2.length > 0 && (
                    <Alert color="danger">{this.state.error2}</Alert>
                )}
                {this.state.msg2.length > 0 && (
                    <Alert color="success">{this.state.msg2}</Alert>
                )}
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleEmail">New password*</Label>
                      <Input onChange={this.handlePasswordInput}
                        type="password" 
                        value={this.state.password}
                        name="pass"
                        placeholder=""
                      />
                    </FormGroup>

                    <FormGroup>
                      <Label for="exampleEmail">Re-enter password*</Label>
                      <Input onChange={this.handleCpasswordInput}
                        type="password" 
                        value={this.state.c_password}
                        name="cpass"
                        placeholder=""
                      />
                    </FormGroup>
                    
                    <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleChangePwd}>Update password</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
    
          </Row>
    
        </Page>
      );
    }
}
export default ProfilePage;
